<script setup>
import {reactive, watch} from "vue";
import {adminEditTag} from '@/assets/js/api'
import {useMessage} from 'naive-ui'

const message = useMessage()
const props = defineProps({
  show: {
    type: Boolean,
    default: false
  },
  data: {
    type: Object,
    default: () => {
      return {
        id: '',
        name: '',
        slug: '',
        description: '',
        status: true
      }
    }
  }
})

const emit = defineEmits(['update:show', 'finish'])

const formData = reactive({
  id: '',
  name: '',
  slug: '',
  description: '',
  status: true
})

watch(() => props.data, (newValue, oldValue) => {
  formData.id = newValue.id
  formData.name = newValue.name
  formData.slug = newValue.slug
  formData.description = newValue.description
  formData.status = newValue.status
})

const formRules = {
  name: {
    required: true,
    message: '请输入标签名称',
    trigger: 'blur'
  },
  slug: {
    required: true,
    message: '请输入标签别名',
    trigger: 'blur'
  },
  description: {
    required: true,
    message: '请输入标签描述',
    trigger: 'blur'
  },
  status: {
    required: true,
    message: '请设置标签状态',
    trigger: 'change'
  }
}

const editTag = () => {
  adminEditTag(formData).then(res => {
    if (res.data.code === 200) {
      message.success("修改成功")
    } else if (res.data.code === 400) {
      message.error(res.data.msg)
    } else if (res.data.code === 401) {
      message.warning(res.data.msg)
    }
    emit('update:show', false)
    formData.id = ''
    formData.name = ''
    formData.slug = ''
    formData.description = ''
    formData.status = true
    emit('finish')
  }).catch(err => {
    message.error(err.message)
  })
}
</script>

<template>
  <n-modal :show="props.show" closable @update:show="emit('update:show', $event)">
    <n-card style="width: 500px">
      <template #header>
        <n-space justify="space-between">
          <span>修改标签</span>
        </n-space>
      </template>
      <n-form :model="formData" :rules="formRules">
        <n-form-item label="标签名称" path="name">
          <n-input v-model:value="formData.name" placeholder="请输入标签名称"/>
        </n-form-item>
        <n-form-item label="标签别名">
          <n-input v-model:value="formData.slug" placeholder="请输入标签别名"/>
        </n-form-item>
        <n-form-item label="标签描述">
          <n-input clearable placeholder="请输入标签描述" rows="4" type="textarea"/>
        </n-form-item>
        <n-form-item label="标签状态" path="status">
          <n-switch v-model:value="formData.status"/>
        </n-form-item>
      </n-form>
      <template #footer>
        <n-space justify="end">
          <n-button @click="emit('update:show', false)">取消</n-button>
          <n-button type="primary" @click="editTag">确定</n-button>
        </n-space>
      </template>
    </n-card>
  </n-modal>
</template>

<style scoped>

</style>