<script setup>
import AddTag from "@/components/AddTag.vue";
import {NButton, NIcon, NTag, useLoadingBar, useMessage, useModal} from "naive-ui";
import {h, reactive} from "vue";
import {
  CommentOutlined,
  FolderOutlined,
  HomeOutlined,
  InboxOutlined,
  SettingOutlined,
  TagOutlined,
  UserOutlined
} from "@vicons/antd";
import {useRouter} from "vue-router";
import {adminDeleteTag, adminGetTag} from "@/assets/js/api";
import EditTag from "@/components/EditTag.vue";

document.title = '标签';

const loadingBar = useLoadingBar()

const message = useMessage()
const router = useRouter()
const modal = useModal()
const renderIcon = (icon) => {
  return () => h(NIcon, null, {default: () => h(icon)});
}

const menuOptions = [
  {
    label: '首页',
    key: 'adminHome',
    icon: renderIcon(HomeOutlined),
  },
  {
    label: '资源',
    key: 'adminResource',
    icon: renderIcon(InboxOutlined),
  },
  {
    label: '分类',
    key: 'adminCategory',
    icon: renderIcon(FolderOutlined),
  },
  {
    label: '标签',
    key: 'adminTag',
    icon: renderIcon(TagOutlined),
  },
  {
    label: '评论',
    key: 'adminComment',
    icon: renderIcon(CommentOutlined),
  },
  {
    label: '用户',
    key: 'adminUser',
    icon: renderIcon(UserOutlined),
  },
  {
    label: '系统',
    key: 'adminSystem',
    icon: renderIcon(SettingOutlined),
  }
]

const handleSelect = (key) => {
  router.push({name: key})
}

const queryData = reactive({
  kwd: '',
  order: 'asc',
  orderby: 'id',
})

const data = reactive({
  showAddTag: false,
  tagData: [],
  showEditTag: false,
  editData: {},
  loading: false
})


const orderOption = [
  {
    label: 'ID',
    value: 'id'
  },
  {
    label: '名称',
    value: 'name'
  },
  {
    label: '创建时间',
    value: 'create_time'
  },
  {
    label: '更新时间',
    value: 'update_time'
  }]
const columns = [
  {
    title: 'ID',
    key: 'id',
    width: 100,
    align: 'center'
  },
  {
    title: '名称',
    key: 'name',
    width: 200,
    align: 'center',
    render(row) {
      return h(NTag, {
        type: 'info',
        size: 'small',
      }, row.name)
    }
  },
  {
    title: '别名',
    key: 'slug',
    width: 200,
    align: 'center'
  },
  {
    title: '创建时间',
    key: 'create_time',
    width: 200,
    align: 'center'
  },
  {
    title: '更新时间',
    key: 'update_time',
    align: 'center'
  },
  {
    title: '操作',
    key: 'action',
    width: 200,
    align: 'center',
    render(row) {
      return h('div', [
        h(NButton, {
          type: 'primary',
          size: 'small',
          secondary: true,
          style: {
            marginRight: '10px'
          },
          onClick: () => {
            data.editData = row
            data.showEditTag = true
          }
        }, {
          default: () => '编辑'
        }),
        h(NButton, {
          type: 'error',
          size: 'small',
          secondary: true,
          onClick: () => {
            deleteTag(row.id)
          }
        }, {
          default: () => '删除'
        })
      ])
    }
  }
]

const getTag = async () => {
  data.loading = true
  loadingBar.start()
  adminGetTag(queryData).then(res => {
    if (res.data.code === 200) {
      data.tagData = res.data.data
      data.loading = false
      loadingBar.finish()
    } else if (res.data.code === 400) {
      data.loading = false
      loadingBar.error()
      message.error(res.data.msg)
    } else if (res.data.code === 401) {
      loadingBar.error()
      data.loading = false
      message.warning(res.data.msg)
    }
  }).catch(err => {
    loadingBar.error()
    data.loading = false
    message.error(err.message)
  })
}
getTag()

const deleteTag = (id) => {
  const m = modal.create({
    title: '删除标签',
    content: '确定删除该标签吗？',
    preset: 'dialog',
    positiveText: '确定',
    negativeText: '取消',
    onPositiveClick: () => {
      adminDeleteTag({id: id}).then(res => {
        if (res.data.code === 200) {
          message.success(res.data.msg)
          getTag()
        } else if (res.data.code === 400) {
          message.error(res.data.msg)
        } else if (res.data.code === 401) {
          message.warning(res.data.msg)
        }
      }).catch(err => {
        message.error(err.message)
      })
    },
    onNegativeClick: () => {
      m.destroy()
    }
  })
}
</script>

<template>
  <n-layout style="padding-right: 10px">
    <n-layout-header style="padding: 24px">
      <n-page-header @back="router.push({name: 'adminHome'})">
        <template #header>
          <n-breadcrumb>
            <n-breadcrumb-item>
              <n-dropdown :options="menuOptions" @select="handleSelect">
                <div>
                  <n-icon :component="HomeOutlined" size="16"/>
                  首页
                </div>
              </n-dropdown>
            </n-breadcrumb-item>
            <n-breadcrumb-item>
              <n-icon :component="TagOutlined" size="16"/>
              标签
            </n-breadcrumb-item>
          </n-breadcrumb>
        </template>
        <template #title>
          标签
        </template>
        <template #extra>
          <n-button @click="data.showAddTag = true">新建标签</n-button>
        </template>
        <template #default>
          <n-grid :cols="4" :x-gap="24" :y-gap="24">
            <n-grid-item :span="1">
              <n-input v-model:value="queryData.kwd" placeholder="请输入标签名称" @update:value="getTag"></n-input>
            </n-grid-item>
            <n-grid-item :span="1" style="display: flex">
              <n-radio-group v-model:value="queryData.order" style="margin-right: 10px" @update:value="getTag">
                <n-radio-button value="asc">升序</n-radio-button>
                <n-radio-button value="desc">降序</n-radio-button>
              </n-radio-group>
              <n-select v-model:value="queryData.orderby" :options="orderOption" style="width: 100px"
                        @update:value="getTag"/>
            </n-grid-item>
          </n-grid>
        </template>
      </n-page-header>
    </n-layout-header>
    <n-layout-content style="padding: 24px 0">
      <n-data-table :columns="columns" :data="data.tagData" :loading="data.loading" :max-height="450"/>
    </n-layout-content>
  </n-layout>
  <EditTag :data="data.editData" :show="data.showEditTag" @finish="getTag" @update:show="data.showEditTag = $event"/>
  <AddTag :show="data.showAddTag" @finish="getTag" @update:show="data.showAddTag = $event"/>
</template>

<style scoped>

</style>